.list-column {

	@include padding(55px);
	@include border-radius(10px);

	background-color: map-get( $map: $colors, $key: pink );

	ul {
		margin: 40px 0 0;

		li {
			font-size: 20px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&:nth-child(n + 3) {
		margin-top: 20px;
	}

	// &:nth-child(1n + 2) {
	// 	background-color: map-get( $map: $colors, $key: lyellow );
	// }
}
